<template>
  <section id="main">
    <header class="cd-header">
      <div class="header-wrapper">
        <div class="logo-wrap">
          <a href="#" class="hover-target"><span>和佑</span>国际医院</a>
        </div>
        <div class="nav-but-wrap">
          <div class="menu-icon hover-target">
            <span class="menu-icon__line menu-icon__line-left"></span>
            <span class="menu-icon__line"></span>
            <span class="menu-icon__line menu-icon__line-right"></span>
          </div>
        </div>
      </div>
    </header>

    <div class="nav">
      <div class="nav__content">
        <ul class="nav__list">
          <li
            class="nav__list-item"
            :class="{ 'active-nav': active == 1 }"
            @click="showStep(1)"
          >
            <a href="#" class="hover-target">护士站</a>
          </li>
          <li
            class="nav__list-item"
            :class="{ 'active-nav': active == 2 }"
            @click="showStep(2)"
          >
            <a href="#" class="hover-target">VIP病房</a>
          </li>
          <li
            class="nav__list-item"
            :class="{ 'active-nav': active == 3 }"
            @click="showStep(3)"
          >
            <a href="#" class="hover-target">普通病房</a>
          </li>
          <li
            class="nav__list-item"
            :class="{ 'active-nav': active == 4 }"
            @click="showStep(4)"
          >
            <a href="#" class="hover-target">过道</a>
          </li>
        </ul>
      </div>
    </div>

    <div class="section full-height over-hide">
      <div class="switch-wrap">
        <div id="viewer"></div>
      </div>
    </div>

    <div class="cursor" id="cursor"></div>
    <div class="cursor2" id="cursor2"></div>
    <div class="cursor3" id="cursor3"></div>
  </section>
</template>

<script>
/**
 * removeMarker   markerId
 */
import { Viewer } from "photo-sphere-viewer";
import "photo-sphere-viewer/dist/photo-sphere-viewer.css";

import { MarkersPlugin } from "photo-sphere-viewer/dist/plugins/markers";
import "photo-sphere-viewer/dist/plugins/markers.css";

export default {
  name: "photoSphere",
  data() {
    return {
      active: 1,
      step2: require("../assets/heyou/VIP病房3.jpg"),
      step1: require("../assets/heyou/护士站2.jpg"),
      step4: require("../assets/heyou/过道2.jpg"),
      step3: require("../assets/heyou/普通病房.jpg"),
      viewer: null,
    };
  },
  mounted() {
    this.initPhotoSphere();
    this.initPage();
  },
  methods: {
    initPage() {
      (function ($) {
        "use strict";

        //Page cursors

        document
          .getElementsByTagName("body")[0]
          .addEventListener("mousemove", function (n) {
            (t.style.left = n.clientX + "px"),
              (t.style.top = n.clientY + "px"),
              (e.style.left = n.clientX + "px"),
              (e.style.top = n.clientY + "px"),
              (i.style.left = n.clientX + "px"),
              (i.style.top = n.clientY + "px");
          });
        var t = document.getElementById("cursor"),
          e = document.getElementById("cursor2"),
          i = document.getElementById("cursor3");
        function n(t) {
          e.classList.add("hover"), i.classList.add("hover");
        }
        function s(t) {
          e.classList.remove("hover"), i.classList.remove("hover");
        }
        s();
        for (
          var r = document.querySelectorAll(".hover-target"), a = r.length - 1;
          a >= 0;
          a--
        ) {
          o(r[a]);
        }
        function o(t) {
          t.addEventListener("mouseover", n), t.addEventListener("mouseout", s);
        }

        //Navigation

        var app = (function () {
          var body = undefined;
          var menu = undefined;
          var menuItems = undefined;
          var init = function init() {
            body = document.querySelector("body");
            menu = document.querySelector(".menu-icon");
            menuItems = document.querySelectorAll(".nav__list-item");
            applyListeners();
          };
          var applyListeners = function applyListeners() {
            menu.addEventListener("click", function () {
              return toggleClass(body, "nav-active");
            });
          };
          var toggleClass = function toggleClass(element, stringClass) {
            if (element.classList.contains(stringClass))
              element.classList.remove(stringClass);
            else element.classList.add(stringClass);
          };
          init();
        })();

        //Switch light/dark

        $("#switch").on("click", function () {
          if ($("body").hasClass("light")) {
            $("body").removeClass("light");
            $("#switch").removeClass("switched");
          } else {
            $("body").addClass("light");
            $("#switch").addClass("switched");
          }
        });
      })(jQuery);
    },
    initPhotoSphere() {
      const that = this;
      this.viewer = new Viewer({
        container: document.querySelector("#viewer"),
        panorama: this.step1,
        mousewheel: false,
        fisheye: 1,
        size: {
          width: "100%",
          height: "100%",
        },
        plugins: [
          [
            MarkersPlugin,
            {
              markers: [],
            },
          ],
        ],
        navbar: ["autorotate", "zoom", "markers", "caption", "fullscreen"],
      });
      this.viewer.on("click", (e, data) => {
        that.markersEditConfig = {
          marker: {
            status: 0, // 0 未发布 1已发布
            title: "", // 标题
            description: "", // 描述
            attachment: {
              type: "img", // 附件的类型 img video link
              imgArr: [],
              videoArr: [], // 视频
              linkText: "", // 文字链接的文字
              link: "", // 链接地址
            },
          },
          jump: {
            angle: "0°",
            targetSceneId: "", // 目标场景id
          },
        };
        // 添加初始标注
        that.addMarker(e, data);
      });
      this.markersPlugin = this.viewer.getPlugin(MarkersPlugin);
    },
    addMarker(e, data) {
      // 添加初始标注 具体标注编辑后会更新
      // currentTabIndex 1: 添加标注 2:添加跳转
      // console.log('添加标注')
      const id = `marker${+new Date()}`;
      const config = {
        id,
        html: '<div><svg xmlns="http://www.w3.org/2000/svg" version="1.1" class="patterns"><circle class="loading-circle-1" cx="50" cy="25" r="10" stroke="#12507bc4" fill="#224e7f" /></svg></div>',
        svgStyle: {},
        longitude: data.longitude,
        latitude: data.latitude,
        anchor: "center center",
        tooltip: {
          // tooltip with custom position
          content: `
              <div class="marker-tooltip-container">
                <p class="marker-title">
                  自定义标题
                </p>
                <p class="marker-description">
                  自定义标题
                </p>
              </div>
            `,
          position: "center top",
        },
        visible: true, // 标注初始显示与否
        type: "marker",
        status: 1,
        isInitialize: true,
        attachment: {
          type: "img",
          imgArr: [],
          videoArr: [],
        },
      };
      this.markersPlugin.addMarker(config);
      // 显示 tooltip
      this.markersPlugin.markers[id].showTooltip();

      // 隐藏 tooltip
      //   this.markersPlugin.markers[id].hideTooltip()
      this.currentMarkerId = id;
    },
    showStep(step) {
      this.active = step;
      let image = null;
      switch (step) {
        case 1:
          image = this.step1;
          break;
        case 2:
          image = this.step2;
          break;
        case 3:
          image = this.step3;
          break;
        case 4:
          image = this.step4;
          break;
        default:
          image = this.step1;
          break;
      }
      this.changeImage(image);
    },
    changeImage(obj) {
      this.viewer.setPanorama(obj, {}, true).then(() => {
        document.querySelector(".menu-icon").click()
      });
    },
  },
};
</script>


<style scoped src="./base.css"></style>
<style>
#main {
  width: 100%;
  height: 100%;
  position: relative;
}
#viewer {
  width: 100%;
  height: 100%;
}
.patterns {
  width: 96px;
  height: 48px;
}
@keyframes morph {
  0% {
    stroke-width: 10;
    stroke-opacity: 0.1;
  }
  25% {
    stroke-width: 30;
    stroke-opacity: 0.3;
  }
  50% {
    stroke-width: 10;
    stroke-opacity: 0.5;
  }
  75% {
    stroke-width: 10;
    stroke-opacity: 0.3;
  }
  100% {
    stroke-width: 10;
    stroke-opacity: 0;
  }
}
.loading-circle-1 {
  -webkit-animation: morph 2s ease infinite;
  animation: morph 2s ease infinite;
}
</style>